<template>
  <div
    class="v-input"
    :class="{
      'v-input--focus': inputFocus,
      'v-input--error':
        rules(value) !== true && (inputFilled || isTouched || validate),
      'v-input--filled': inputFilled,
    }"
  >
    <div class="v-input__control">
      <label class="v-input__label body-1">{{ placeholder }}</label>
      <input
        class="body-1"
        @focus="inputFocus = true"
        @click="isTouched = true"
        @blur="inputFocus = false"
        @input="updateValue($event)"
        :value="value"
      />
      <span
        class="v-input__type"
        :class="{
          'v-input__type--hidden': required || inputFilled || inputFocus,
        }"
        >"Optional"</span
      >
    </div>

    <span class="input__border"></span>
    <small class="v-input__messages">{{
      rules(value) !== true && (inputFilled || isTouched || validate)
        ? rules(value)
        : " "
    }}</small>
    <!-- {{rules(value)===true}} -->
  </div>
</template>

<script>
export default {
  name: "mainInput",
  data() {
    isTouched: false;
  },
  props: {
    rules: {
      type: Function,
      default: (val) => true,
    },
    required: {
      type: Boolean,
      default: false,
    },
    placeholder: String,
    value: String,
    validate: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      inputFocus: false,
    };
  },
  methods: {
    updateValue(e) {
      this.$emit("update:value", e.target.value);
    },
    // validate(){
    //   this.isTouched = true;
    // }
  },
  computed: {
    inputFilled() {
      return this.value.length;
    },
    isValid() {
      return this.rules(this.value) === true;
    },
  },
};
</script>

<style lang='scss' scoped>
@import "../stylesheets/main.scss";
$inputPrimary: rgba(0, 0, 0, 0.3);
$inputActive: rgba(0, 0, 0, 1);
$inputError: #f00;
