      min-width: 0;
      width: 100%;
      border: none;
      border-bottom: thin solid $inputPrimary;
      position: relative;
      z-index: 1;
      background: transparent;
    }
  }
  &__label {
    position: absolute;
    z-index: 0;
    transition: transform 0.3s cubic-bezier(0.25, 0.8, 0.5, 1),
      color 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
    color: $inputPrimary;
    font-size: 0.875rem;
    left: 0;
    right: auto;
    transform-origin: top left;
  }
  &__messages {
    font-size: 0.75rem;
    display: inline-block;
    min-height: 0.75rem;
    position: absolute;
    left: 0;
    bottom: 0;
  }
  &__type {
    position: absolute;
    right: 0;
    top: 12px;
    border: 1px solid #acacac;
    color: #acacac;
    font-size: 0.75rem;
    line-height: 1;
    padding: 3px 6px;
    // border-radius: 10px;
    z-index: 0;
    opacity: 1;
    transition: opacity 0.2s cubic-bezier(0.23, 1, 0.32, 1);
    &--hidden {
      opacity: 0;
    }
  }
  &--focus {
    .v-input__control {
      input {
        border-bottom-color: $inputActive;
      }
    }
    .v-input__label {
      color: $inputActive;
    }
  }
  &--focus,
  &--filled {
    .v-input__label {
      transform: scale(0.85) translate3d(0, -22px, 0);
    }
  }
  &--error {
    color: $inputError;
    border-color: $inputError;
    .v-input__control {
      input {
        border-bottom-color: $inputError;
      }
    }
    .v-input__label {
      color: $inputError;
      animation: 3s linear 1s schakeX;
    }
  }
}
</style>