сатель и журналист. Автор семи книг, среди которых – \"Запах шахмат\", \"День Банана\" и \"Дорогое удовольствие\". Давно влюблен в Одессу и считает ее своим вторым домом. ")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-md-4",attrs:{"data-aos":"fade-up","data-aos-offset":"500"}},[_c('h2',{staticClass:"my-3"},[_vm._v(" История. Савва. Драйв"),_c('span',{staticClass:"text--primary"},[_vm._v(".")])]),_c('p',{staticClass:"mb-3"},[_vm._v(" \"Мы дружим с Саввой – страшно сказать как долго – наверно, уже лет пятнадцать. Несколько лет назад я даже успел поработать его PR-директором, а также выступил редактором обеих его книг. Поэтому когда он предложил мне помочь ему написать историю его жизни, я долго не раздумывал. Прежде мне не приходилось выступать чьим-то биографом, но в случае с Саввой наша работа над книгой превратилась в очень драйвовый, веселый процесс\". ")]),_c('h4',[_vm._v("Антон Фридлянд")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-8 offset-md-2"},[_c('h2',{staticClass:"my-3"},[_vm._v(" Друзья о Савве"),_c('span',{staticClass:"text--primary"},[_vm._v(".")]),_vm._v(" "),_c('br')])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-8 offset-md-2",attrs:{"data-aos":"fade-up","data-aos-offset":"500"}},[_c('p',[_vm._v(" Одессит – самый требовательный клиент мира. Это я утверждаю ответственно, так как сам в Одессе вырос. С форшмаком матери каждый житель нашего города впитал высочайший уровень качества и вкуса пищи. И всю дальнейшую жизнь, в какой бы стране не оказался, он ищет чтоб хотя бы не перемаслено, уже молчим о том, чтобы близко к домашнему. Попытка читателя оценить деловой путь Саввы Либкина будет неудачной без понимания описанной выше тонкости. Помните, что Савве удалось накормить людей с максимально высоким уровнем придирчивости к каждому помидору в салате. Если уж покорил эту высоту, дальше перед тобой открыты все пути, и я от души желаю моему дорогому другу процветания. ")]),_c('h3',[_vm._v("-Андрей Ставницер")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-8 offset-md-2 text-end",attrs:{"data-aos":"fade-up","data-aos-offset":"500"}},[_c('p',[_vm._v(" Первые две книги Саввы были с рецептами. Но он не только повар, не только бизнесмен. Он художник. Поэтому к каждому ресторану, каждому блюду и почти к каждой фразе он относится как к творческому акту. Поэтому в ресторанах не только красиво, но и интересно. Вкусно. В любых обстоятельствах. Книга это коллекция абсолютно одесских вызовов, ситуаций, находок и персонажей, переживаний и достижений. Не только передаёт дух времени, но так же содержит рецепты. Правда не блюд, а жизни. ")]),_c('h3',[_vm._v("-Андрей Федорив")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-8 offset-md-2",attrs:{"data-aos":"fade-up","data-aos-offset":"500"}},[_c('p',[_vm._v(" В Одессу едут за морем, атмосферой, слушать как говорят местные жители и за одесской кухней. Савва Либкин и его гастрономические проекты Компот, Тавернетта и Дача часть этого явления. Рестораны Одесской кухни можно встретить в Нью-Йорке, Хабаровске, Тель-Авиве, Киеве и Берлине. Это единственная кухня имени Города, где она родилась, все остальные кухни имени стран, китайская, мексиканская, итальянская. Я прочитал книгу с удовольствием, она интересна, после неё отличное послевкусие, и хочется пить чай с черешневым вареньем в Даче, читать эту книгу, размышлять и делать выводы. Когда Одесса и Дача временно недоступны, можно просто открыть эту книгу в любой части мира и представить, что ты уже там. ")]),_c('h3',[_vm._v("-Евгений Черняк")])])])