<script>
// import buttonSimple from "../components/buttons/buttonSimple.vue";

export default {
  components: {
    // buttonSimple,
  },
  data() {
    return {
      showNavbar: true,
      lastScrollPosition: 0,
      loadingPercent: 0,
      loadTime: 0,
      interval: null,
      loading: true,
      items: [
        {
          name: "О книге",
          link: "/home",
        },
        {
          name: "Авторы",
          link: "/portfolio",
        },
        // {
        //   name: "Services",
        //   link: "/services",
        // },
        {
          name: "Заказать",
          link: "/about",
        },
        // {
        //   name: "Get in touch",
        //   link: "/about",
        // },
      ],
    };
  },
  methods: {
    onScroll() {
      const currentScrollPosition =
        window.pageYOffset || document.documentElement.scrollTop;
      // if (currentScrollPosition > 0) {
      //   return;
      // }
      // // Stop executing this function if the difference between
      // // current scroll position and last scroll position is less than some offset
      // if (currentScrollPosition > 0) {
      //   return;
      // }
      this.showNavbar = currentScrollPosition < 60;
      // this.lastScrollPosition = currentScrollPosition;
    },
    doProgress() {
      let step = this.loadTime / 100;
      this.interval = setInterval(() => {
        this.loadingPercent++;
      }, step);
    },
    changeLang(item) {
      console.log(item.value, this.$store.state.currentLanguage);
      this.$router.push({
        name: this.$route.name,
        params: { locale: item.value },
      });
      this.$store.commit("setCurrentLanguage", item);
      // this.$translate.setLang(item.value);
    },
  },
  created() {
    let perfData = window.performance.timing;
    let estimatedTime = Math.abs(
      perfData.loadEventEnd - perfData.navigationStart
    );
    this.loadTime = parseInt((estimatedTime / 1000) % 60) * 100;
    this.doProgress();
  },
  watch: {
    loadingPercent(val) {
      if (val >= 100) {
        this.loading = false;
        clearInterval(this.interval);
      }
    },
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },

  // beforeDestroy() {
  //   window.removeEventListener("scroll", this.onScroll);
  // }
};
</script>

